export class ProductsData {
    constructor(events) {
        this.events = events;
    }
    // Инициализация массива товаров
    set pageStore(products) {
        this.store = products;
        this.events.emit('products:set');
    }
    get pageStore() {
        return this.store;
    }
    getProduct(productId) {
        return this.store.find((item) => item.id === productId);
    }
}
