import { createElement, ensureElement, formatNumber } from '../../utils/utils';
import { Component } from '../base/Component';
export class Cart extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._list = ensureElement('.basket__list', container);
        this._total = ensureElement('.basket__price', container);
        this._button = ensureElement('.basket__button', container);
        if (this._button) {
            this._button.addEventListener('click', () => {
                events.emit('order:open');
            });
        }
        this.products = [];
    }
    set products(products) {
        if (products.length) {
            this._list.replaceChildren(...products);
        }
        else {
            this._list.replaceChildren(createElement('p', {
                textContent: 'Корзина пустая',
            }));
        }
    }
    set index(products) {
        if (products) {
            //products.reverse();
            for (let i = 0; i < products.length; i++) {
                const index = products[i].querySelector('.basket__item-index');
                index.textContent = `${i + 1}`;
            }
        }
    }
    set total(total) {
        this.setText(this._total, `${formatNumber(total, ' ')} синапсов`);
    }
    set isEmpty(value) {
        this.setDisabled(this._button, value);
    }
}
