import { ensureElement } from '../../utils/utils';
import { ProductView } from './ProductView';
export class ProductModal extends ProductView {
    constructor(container, events) {
        super(container, events);
        this._description = ensureElement('.card__text', container);
    }
    addEventListeners() {
        this._button.addEventListener('click', () => this.events.emit('product:buy', { id: this.id }));
    }
    set description(value) {
        this.setText(this._description, value);
    }
    set inCart(value) {
        this.setDisabled(this._button, value);
        if (value === true) {
            this.setText(this._button, 'Уже в корзине');
        }
        else {
            this.setText(this._button, 'В корзину');
        }
    }
}
