import { ensureAllElements } from '../../utils/utils';
import { Form } from '../common/Form';
export class OrderData extends Form {
    constructor(container, events) {
        super(container, events);
        this._paymentBtns = ensureAllElements('.button_alt', this.container);
        this._paymentBtns.forEach((button) => {
            button.addEventListener('click', () => {
                this.orderMethodPay = button.name;
                this.onInputChange('payment', button.name);
            });
        });
    }
    set orderMethodPay(name) {
        this._paymentBtns.forEach((button) => {
            this.toggleClass(button, 'button_alt-active', button.name === name);
        });
    }
    set orderAddress(value) {
        this.container.elements.namedItem('address').value =
            value;
    }
    set valid(value) {
        this.setDisabled(this._submit, !value);
    }
}
