import { ensureElement } from '../../utils/utils';
import { Product } from '../common/Product';
export class ProductView extends Product {
    constructor(container, events) {
        super(container, events);
        this.container = container;
        this.CategoryProd = {
            'софт-скил': 'card__category_soft',
            'хард-скил': 'card__category_hard',
            дополнительное: 'card__category_additional',
            другое: 'card__category_other',
            кнопка: 'card__category_button',
        };
        this._category = ensureElement('.card__category', container);
        this._image = ensureElement('.card__image', container);
    }
    addEventListeners() {
        this.container.addEventListener('click', () => this.events.emit('product:select', { id: this.id }));
    }
    set category(value) {
        this.setText(this._category, value);
        if (this._category) {
            this._category.className = `card__category ${this.CategoryProd[value]}`;
        }
    }
    set image(value) {
        this.setImage(this._image, value);
    }
}
