import { ensureElement, formatNumber } from '../../utils/utils';
import { Component } from '../base/Component';
export class Success extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._closeBtn = ensureElement('.order-success__close', container);
        this._summ = ensureElement('.order-success__description', container);
        this._closeBtn.addEventListener('click', () => {
            events.emit('success:submit');
        });
    }
    set total(total) {
        this.setText(this._summ, `Списано ${formatNumber(total, ' ')} синапсов`);
    }
}
