import { ensureElement } from '../../utils/utils';
import { Component } from '../base/Component';
export class MainPage extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._counter = ensureElement('.header__basket-counter', container);
        this._basketBtn = ensureElement('.header__basket', container);
        this._store = ensureElement('.gallery', container);
        this._parent = ensureElement('.page__wrapper', container);
        this._basketBtn.addEventListener('click', () => {
            this.events.emit('basket:open');
        });
    }
    set products(items) {
        this._store.replaceChildren(...items);
    }
    set counter(value) {
        this.setText(this._counter, String(value));
    }
    set lock(value) {
        if (value) {
            this.toggleClass(this._parent, 'page__wrapper_locked', true);
        }
        else {
            this.toggleClass(this._parent, 'page__wrapper_locked', false);
        }
    }
}
