import { Component } from '../base/Component';
import { ensureElement } from '../../utils/utils';
export class Modal extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._closeButton = ensureElement('.modal__close', container);
        this._content = ensureElement('.modal__content', container);
        this._closeButton.addEventListener('click', this.closeModal.bind(this));
        this._content.addEventListener('click', (evt) => evt.stopPropagation());
        this.container.addEventListener('mousedown', (evt) => {
            if (evt.target === evt.currentTarget) {
                this.closeModal();
            }
        });
    }
    set content(value) {
        this._content.replaceChildren(value);
    }
    openModal() {
        this.toggleClass(this.container, 'modal_active', true);
        this.events.emit('modal:open');
    }
    closeModal() {
        this.toggleClass(this.container, 'modal_active', false);
        this.content = null;
        this.events.emit('modal:close');
    }
    render(data) {
        super.render(data);
        this.openModal();
        return this.container;
    }
}
